main {
  position: relative;
}

.grid-overlay {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 2;
  overflow: hidden;
}

/*
  * Getting the proportions correct and allowing enough rows to cover
  * excess height
  *
  * To get to 296px width the parent container (grid overlay) must be at a
  * width of 1256px, with for columns with a gutter of 24px.
  *
  * In the case of the rows, 16rem is as near as I can get using a rem
  * unit to the designed unit of 296px
  */
.grid-visible .master-grid {
  width: 100%;
  pointer-events: none;
  grid-template-rows: repeat(30, 11.5rem);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem 2rem;
}

@media (min-width: 1332px) {
  .grid-visible .master-grid {
    grid-template-rows: repeat(30, 24rem);
    grid-gap: 2rem 3rem;
  }
}

.grid-visible .master-grid > div {
  border: 1px solid;
}

@media (min-width: 978px) {
  .grid-visible .master-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1332px) {
  .grid-visible .master-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (min-width: 1656px) {
  .grid-visible .master-grid {
    grid-template-columns: 21.143rem 0.5fr 0.5fr 21.143rem 21.143rem 21.143rem;
  }
}

@media (min-height: 30rem) {
  .posts-nav-section {
    position: sticky;
    bottom: 0;
    background: #FFF;
    margin-top: 2rem;
  }
}

.posts-nav {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 2rem;
}

.posts-nav .prev::before,
.posts-nav .next::after {
  content: "";
  border-top: 0.1rem solid #000;
  border-left: 0.1rem solid #000;
  width: 1.5rem;
  height: 1.5rem;
  grid-row: span 2;
  align-self: center;
}

.posts-nav .prev::before {
  transform: rotate(-45deg);
}

.posts-nav .next::after {
  transform: rotate(135deg);
  justify-self: flex-end;
}

.posts-nav a {
  display: grid;
  grid-template-rows: auto 1fr;
  grid-column-gap: 1rem;
  text-decoration: none;
  text-transform: capitalize;
  color: inherit;
  font-size: 1.167rem;
  line-height: 1.5rem;
  padding-top: 1.43672rem;
  margin-top: 0rem;
  margin-bottom: 1.31328rem;
}

.posts-nav a.prev {
  grid-template-columns: 1rem 1fr;
}

.posts-nav a.prev > span {
  grid-column: 2;
}

.posts-nav a.next {
  grid-template-columns: 1fr 1rem;
  grid-column: 2;
}

.posts-nav a.next > span {
  grid-column: 1;
  text-align: right;
}

.posts-nav a > span:first-child {
  font-weight: bold;
}

.posts-nav a > span:last-child {
  grid-row: 2;
}

.posts-nav a:hover::before, .posts-nav a:hover::after {
  border-color: inherit;
}

